.img {
  /* transform-origin: right bottom 50px; */
  transition: all 0.5s linear;
  background: darkorchid;
  /* height: 40px; */
  position: relative;
  /* animation: LOH 3000ms linear infinite alternate; */
}

.img::before,
.img::after {
  color: white;
  content: 'qweqwe';
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  transition: all 0.5s linear;
}

.img::before {
  background: darkorchid;
  transform-origin: center top;
  transform: rotateX(90deg) translateY(-20px);
}

.img::after {
  background: black;
}

.img:hover::after {
  transform-origin: center bottom;
  transform: rotateX(90deg) translateY(20px);
}

.img:hover::before {
  transform: rotateX(0) translateY(0);
}

@keyframes LOH {
  0% {
    transform: translateX(0) rotate(0);
  }
  25% {
    transform: translateX(225px) rotate(90deg);
  }
  50% {
    transform: translateX(456px) rotate(180deg);
  }
  75% {
    transform: translateX(681px) rotate(270deg);
  }
  100% {
    transform: translateX(929px) rotate(360deg);
  }
}
