.link {
  position: relative;

  /* background: darkorchid; */
}
/* 'Лицо' */
.link::before,
.link::after {
  color: white;
  content: attr(data-name);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  transition: all 0.3s linear;
  font-size: 1.125rem;
  line-height: 1.75rem;
  text-align: center;
}

.link::before {
  background: darkorchid;
  transform-origin: center top;
  transform: rotateX(90deg) translateY(-16px);
}

.link::after {
  background: black;
}

.link:hover::after {
  transform-origin: center bottom;
  transform: rotateX(90deg) translateY(16px);
}

.link:hover::before {
  transform: rotateX(0) translateY(0);
}
